.app-wrapper {
    display: grid;
    grid-template-areas: "h h" "n c ";
    width: 1200px;
    grid-template-rows: 70px 1fr;
    grid-auto-columns: 2fr 10fr;
    grid-gap: 10px;
    margin: 0 auto;

}

.app-wrapper-content {
    grid-area: c;
    background-color: #05ffca;

}





